import React from "react";
import { Cell, Div, Avatar, Link, Button } from "@vkontakte/vkui";
import Icon12Fire from "@vkontakte/icons/dist/12/fire";
import Icon16Add from "@vkontakte/icons/dist/16/add";

const TopGroupsItem = ({ idVk, id, name, rating, photoURL, addGroupTop, scheme }) => {
  const urlGroup = "https://vk.com/club" + idVk;

  return (
    <Div
      className={
        scheme === "bright_light"
          ? "itemShop_bright_light"
          : "itemShop_space_gray"
      }
    >
      <span style={{ paddingLeft: "15px" }}>{id}</span>
      <Cell
        style={{ width: "100%" }}
        before={
          photoURL ? (
            <Link href={urlGroup} target="_blank">
              <Avatar src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        description={
          <Div className="itemMainTopGroupsBalance">
            <Div className="iconPornCoin16px" />
            <Div style={{ whiteSpace: "nowrap" }}> 5555555 </Div>
          </Div>
        }
        asideContent={
          <Div id={id} onClick={addGroupTop}>
            <Icon16Add fill="green" />
          </Div>
        }
      >
        <Div style={{ marginLeft: "10px" }}>{name}</Div>
      </Cell>
    </Div>
  );
};

export { TopGroupsItem };
